
import {api} from 'api/api.js'
export default {
	onLaunch: function() {
		console.log('App Launch');




		uni.getSystemInfo({
			success: function(res) {
				getApp().globalData.top = res.statusBarHeight; //获取状态栏的高度
				getApp().globalData.windowWidth = res.windowWidth;
				getApp().globalData.windowHeight = res.windowHeight;
			}
		});
		getApp().globalData.session_id = uni.getStorageSync("session_id");
		getApp().globalData.uid = uni.getStorageSync("uid");
	},
	onShow: function() {
		console.log('App Show');
	},
	onHide: function() {
		console.log('App Hide')
	},
	globalData: {
		top: 0,
		windowWidth: 0,
		windowHeight: 0,
		session_id:"",
		uid:0
		
	}
}
